<style>
  .timepicker__container {
    position: relative;
  }
</style>

<template>
  <div class="timepicker__container">
    <input type="text" value="{{time_formatted}}" class="form-control" @click="showTimepicker">
    <input type="hidden" name="{{name}}" value="{{time_raw}}">
    <timepicker-agenda :visible="isVisible" :time="date" @change="selectTime" @cancel="hideTimepicker"></timepicker-agenda>
  </div>
</template>

<script type="text/babel">
  import moment from 'moment'
  import TimepickerAgenda from './TimepickerAgenda.vue'

  moment.locale('fr')

  export default {
    components: {TimepickerAgenda},
    props: {
      value: {type: String, required: true},
      name: {type: String},
      format: {type: String, default: 'HH:mm:ss'}
    },
    data: function () {
      return {
        date: moment(this.value, 'HH:mm:ss'),
        isVisible: false
      }
    },
    computed: {
      time_formatted: function () {
        return this.date.format(this.format)
      },
      time_raw: function () {
        return this.date.format('HH:mm:ss')
      }
    },
    methods: {
      selectTime: function (hour, minute) {
        if (hour >= 0 && hour <= 23 && minute >= 0 && minute <= 59) {
          let date = this.date.clone()
          date.hour(hour)
          date.minute(minute)
          this.date = date
        }
        this.hideTimepicker()
      },
      showTimepicker: function () {
        this.isVisible = true
        setTimeout(() => document.addEventListener('click', this.hideTimepicker), 0)
      },
      hideTimepicker: function () {
        this.isVisible = false
        document.removeEventListener('click', this.hideTimepicker)
      }
    }
  }
</script>


