<style lang="scss">

  .timepicker {
    position: absolute;
    top: 100%;
    width: 210px;
    z-index: 5;
    background-color: #fff;
    box-shadow: 0 14px 45px rgba(0, 0, 0, 0.25), 0 10px 18px rgba(0, 0, 0, 0.22);
  }

  .timepicker__header {
    background-color: #ab1919;
    color: #FFF;
    padding: 20px;
    height: 55px;
  }

  .timepicker__time {
    padding: 15px;
    margin-left: 40px;
    margin-top: 10px;
    div {
      display: inline;
    }
    input {
      width: 40px;
      height: 40px;
      border: none;
      font-size: 32px;
      color: #ab1919;
      &:focus {
        outline: none;
      }
    }
  }

  .timepicker__hour:after {
    content: ':';
  }

  .timepicker__minute {
    margin-left: 5px;
  }

  .timepicker__actions {
    text-align: right;
    padding: 8px;
  }

  .timepicker__actions button {
    border: none;
    background-color: transparent;
    display: inline-block;
    cursor: pointer;
    outline: none;
    color: rgba(223, 28, 28, 0.8);
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 500;
    min-width: 88px;
    line-height: 36px;
    text-align: center;
    -webkit-appearance: none;
    transition: all 0.3s;

    &:hover {
      background-color: rgba(223, 28, 28, 0.8);
    }

  }


</style>

<template>
  <div class="timepicker" v-if="visible" @click.stop>
    <div class="timepicker__header">
      <div class="timepicker__select">
        Sélectionner une heure
      </div>
    </div>

    <div class="timepicker__time">
      <div class="timepicker__hour">
        <input type="text" class="timepicker__hour__input" v-model="hour">
      </div>
      <div class="timepicker__minute">
        <input type="text" class="timepicker__minute__input" v-model="minute">
      </div>
    </div>

    <div class="timepicker__actions">
      <button type="button" @click="cancel">Annuler</button>
      <button type="button" @click="submit">Ok</button>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      time: {type: Object},
      visible: {type: Boolean, default: true}
    },
    data: function () {
      return {
        hour: '',
        minute: ''
      }
    },
    ready: function () {
      this.hour = this.time.format('HH')
      this.minute = this.time.format('mm')
    },
    methods: {
      submit: function () {
        this.$dispatch('change', this.hour, this.minute)
      },
      cancel: function () {
        this.$dispatch('cancel')
      }
    }
  }
</script>


